<template style="margin-bottom: 50%;">
 
 <tr  v-if="qnaUserid==uid || uid=='admin'" style="cursor: pointer" v-on:click="moveDetail(`/qna/read?qnaNo=${qnaNo}`);">
    <td><img style = "height:65%" src="../images/user1.png"></td>
    <td>{{ qnaNo }}</td>
    <td >
      {{ qnaTitle }}
    <td>{{ qnaUserid }}</td>
    <td>{{ getFormatDate(qnaDatetime) }}</td>
     
  </tr>
  <tr  v-else >
    <td ></td>
    <td>{{ qnaNo }}</td>
    <td >{{ qnaTitle }}</td>
    <td >{{ qnaUserid }}</td>
    <td >{{ getFormatDate(qnaDatetime) }}</td>
   
    
  </tr>
</template>

<script>
import moment from 'moment';
export default {
  name: 'row',
  props: {
    qnaNo: { type: Number },
    qnaUserid: { type: String },
    qnaTitle: { type: String },
    qnaDatetime: { type: String },
    uid: { type : String },
  },
  methods: {
    getFormatDate(qnaDatetime) {
      return moment(new Date(qnaDatetime)).format('YYYY.MM.DD');
    },
    moveDetail(url){
   this.$router.push(url);
    }
  },
};
</script>
